







































































































































































































































































































































































.editor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0 2rem;
}
.title {
  font-size: 2.5em;
  margin: 2rem auto 1rem;
  > input {
    width: 500px;
    margin: 0;
    padding: 0 1.5rem;
    font-family: inherit;
    color: inherit;
    font-size: inherit;
    height: 50px;
    background-color: #222;
    border: 2px solid #111;
    outline: 0;
  }
  > small {
    font-size: 0.75em;
    > a {
      color: inherit;
      text-decoration: none;
      outline: 0;
    }
  }
}
.meta {
  margin: 0 auto 1rem;
  font-size: 0.75em;
}
.wrapper, .toolbar {
  max-width: 1280px;
}
.wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  > div {
    width: 50%;
  }
}
.toolbar {
  display: flex;
  width: 100%;
  background: #222;
  font-size: 1.25em;
  > div {
    display: flex;
    justify-content: space-between;
    width: 50%;
    &:first-child {
      border-right: 1px solid #111;
    }
    &:last-child {
      border-left: 1px solid #111;
    }
    > div {
      display: flex;
      align-items: center;
    }
  }
  button, input {
    margin: 0 0.1rem;
    padding: 0 1.5rem;
    font-family: inherit;
    font-size: inherit;
    height: 50px;
    background-color: #222;
    color: inherit;
    border: 2px solid #111;
    border-top: 0;
    border-bottom: 0;
    outline: 0;
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  button {
    cursor: pointer;
    transition: background-color ease-out .15s, opacity ease-out .15s;
    will-change: background-color, opacity;
    &.active, &:hover {
      background-color: #333;
    }
    &.warning {
      background-color: #822;
    }
    &[disabled] {
      opacity: .5;
      cursor: default;
      &:hover {
        background-color: #222;
      }
    }
  }
  input {
    width: 5rem;
    padding: 0 0.75rem;
    &[type=color] {
      padding: 0;
      cursor: pointer;
    }
    &[type=number] {
      width: 3.25rem;
      border-left: 0;
      &::-webkit-inner-spin-button,
      &::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
    }
  }
  > div > div:first-child {
    button, input {
      &:first-child {
        border-left: 0;
      }
    }
  }
  > div > div:last-child {
    button, input {
      &:last-child {
        border-right: 0;
      }
    }
  }
}
